    overflow: hidden;

}


.navbar-item ul li{
    float: left;    
    margin-left: 20px;
}

.allNotes-container{
    margin-top: 50x;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    /* justify-content: space-between; */
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;

    font-family: 'Anton', sans-serif;
    font-family: 'Roboto', sans-serif;

    

}

.block-container{
    background-color:rgb(255, 219, 88);
    width: 180px;
    margin: 10px;
    height:250px;
    border-radius: 15px;
    box-shadow: 10px 10px 8px  rgba(80, 80, 80, 0.2);
}

.block-title{
    margin: 10px;
    margin-top: 0px;
    font-size: 20px;
    -webkit-line-clamp: 2; /* number of lines to show */
}

.block-body{  
    margin: 10px;
    height: 150px;
}

.block-date{
    margin: 10px;
    margin-top: 10px;
   font-size: 13px;
   color: rgb(63, 60, 60);
}

.block-delete{
    margin: 0;
    text-align: end;
    margin-right: 10px;
    margin-top: 10px;
}

.delete-Text{
    cursor: pointer;

}

.CreateNote{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    /* background-color: red; */

    font-family: 'Anton', sans-serif;
    font-family: 'Roboto', sans-serif; 

}

.CreteNoteHere{
    text-decoration: none;
    color: black;
}

.CreateNote-button{
    margin: 10px;
    width: 200px;
    height: 50px;
    border-radius: 10px;
    border: none;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    background-color: rgba(150, 236, 150, 0.959);
    font-size: 20px;
}

.Link-CR{
    text-decoration: none;
    color: black;
}

.createNote-Container{
    height: 550px;
    width: 400px;
    /* box-shadow: 10px 10px 8px  rgba(80, 80, 80, 0.2); */
    margin: 10px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    border-radius: 10px;
    background-color: black;
}

.createNote-Text{
    height: 60px;
    width: 405px;
    /* box-shadow: 10px 10px 8px  rgba(80, 80, 80, 0.2); */
    margin: 10px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    background-color: rgb(253, 228, 0);
    font-size: 25px;
    border-radius: 10px;
    cursor: pointer;

}

.JustText{
    text-align: center;
    padding: 20px;

}

.createNote-block-title{
    margin: 10px;
    border-radius: 15px;
    height: 100%;
    /* background-color: rgb(66, 66, 68); */
    padding: 10px;
}

.createNote-block-title input{
    width: 365px;
    height: 60px;
    font-size: 28px;
    padding-left: 10px;
    border: none;
    background-color: rgba(0, 0, 0, 0);
    border-color: rgba(0, 0, 0, 0);
    color: rgba(247, 247, 247, 0.849);
}

.createNote-block-description{
    margin: 10px;
    border-radius: 15px;
    padding: 20px;

    height: 70%;
    background-color: red;

}

.createNote-description {
    font-size: 20px;
    padding: 10px;
    border: none;
    background-color: rgba(0, 0, 0, 0);
    color: rgba(224, 221, 221, 0.877);

}
